.Placeholder {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: calc(var(--safe-area-inset-top) + var(--panelheader_height_ios) + 100vh / 812 * 72) 0 0 28px
}

.Main {
    margin: calc(var(--safe-area-inset-top) + var(--panelheader_height_ios)) 28px 0 28px;
}

.Cards__Container {
    justify-content: space-between;
}

.ButtonPlay {
    bottom: calc(var(--safe-area-inset-bottom) + 38px);
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
}

.Gallery, .Rating {
    margin: 0 28px;
}

.Search {
    margin-top: 47px;
}

.Search h1 {
    margin: 48px 0;
}

.Game {
    margin: calc(var(--safe-area-inset-top) + var(--panelheader_height_ios)) 28px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.Comments_Container {
    width: 100%;
}

.Comment_Input {
    margin-top: 14px;
}

.Comments {
    max-height: 114px;
}

.Result {
    margin: calc(var(--safe-area-inset-top) + var(--panelheader_height_ios) + 5.78817734vh) 28px 0;
}

.Result .Button--lvl-secondary {
    position: absolute;
    bottom: calc(var(--safe-area-inset-bottom) + 42px);
}